import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react';
import { isWebpSupported } from 'react-image-webp/dist/utils/index.js';
import { fadeInSlide } from '../animations/anime-js.jsx';

//* ------------------------ Component's MainSlide -----------------------------
import { buildSwiper } from '../layouts/build-swiper.js';
import { mainSlide } from '../layouts/main-slide.js';
import { AudioPlayer } from './AudioPlayer.jsx';
//* ----------------------------------------------------------------------------
import Showreel from '../ontext/Showreel.jsx';

export const MainSlide = ({ baseUrl }) => {
	useEffect(() => {
		buildSwiper();
		mainSlide();
		// Showreel();
	}, []);
	useEffect(() => {
		const slideWrappers = document.querySelectorAll(
			'.main-slide__slide-wrapper',
		);
		if (!slideWrappers.length) return; // ,  -
		// 

		//        
		const firstSlideWrapper = slideWrappers[0];
		const isActive = firstSlideWrapper.classList.contains(
			'swiper-slide-active',
		);

		if (isActive) {
			//    ,  
			fadeInSlide();
		}

		//     -
		slideWrappers.forEach((slideWrapper) => {
			const observer = new MutationObserver((mutations) => {
				mutations.forEach((mutation) => {
					if (
						mutation.type === 'attributes' &&
						mutation.attributeName === 'class'
					) {
						const isActive = slideWrapper.classList.contains(
							'swiper-slide-active',
						);
						if (isActive) {
							//  -   'swiper-slide-active',
							//  
							fadeInSlide();
						}
					}
				});
			});

			observer.observe(slideWrapper, { attributes: true });
			//     
			return () => {
				observer.disconnect();
			};
		});
	}, []);

	const getPath = (fileName) => {
		return `${baseUrl}/${fileName}`;
	};

	// useEffect(() => {
	// 	const video = videoRef.current;

	// 	if (video) {
	// 		const handleCanPlay = () => {
	// 			video.play().catch((error) => {
	// 				console.error(' :', error);
	// 			});
	// 		};

	// 		video.muted = true; //  mute 
	// 		video.addEventListener('canplay', handleCanPlay);

	// 		return () => {
	// 			video.removeEventListener('canplay', handleCanPlay);
	// 		};
	// 	}
	// }, []);  
	const videoRef = useRef(null);
	const [isPlaying, setIsPlaying] = useState(false);

	useEffect(() => {
		const video = videoRef.current;

		if (video) {
			//  mute 
			video.muted = true;
			console.log('Muted:', video.muted);

			//    play
			const handleCanPlay = () => {
				video.play().catch((error) => {
					console.error(' :', error);
				});
			};

			video.addEventListener('canplay', handleCanPlay);

			return () => {
				video.removeEventListener('canplay', handleCanPlay);
			};
		}
	}, []);

	const handlePlay = () => {
		const video = videoRef.current;
		if (video) {
			video.play().catch((error) => {
				console.error(' :', error);
			});
			setIsPlaying(true);
		}
	};


	return (
		<div className="main-slide">
			<div className="main-slide__body _swiper">
				<div className="main-slide__slide-wrapper">
					<div className="main-slide__slide-image _img">
						<video
							ref={videoRef}
							src="/img/audio/showreel-1.mp4"
							id="showreel-1"
							loop
							muted
							preload="auto"
							autoPlay
						/>
						{!isPlaying && (
							<button className='btn-play' onClick={handlePlay}> </button>
						)}
					</div>
					<div className="main-slide__content _container">
						<h1 className="main-slide__title el-slidetitle h1_01901">
							<span> </span>
							<span>&nbsp; </span>
							<span></span> &nbsp;
						</h1>
						<div className="main-slide__text t_01901">
							 -   , &nbsp; 
							  &nbsp;  &nbsp;
							,     &nbsp;
							&nbsp; , &nbsp;, 
							&nbsp;.
						</div>
					</div>
				</div>
				<div className="main-slide__slide-wrapper">
					<div className="main-slide__slide-image _img">
						<picture>
							{isWebpSupported()
								? (<img src={getPath('./img/main/slides/slide_4.webp')}
									alt="slide-4" />)
								: (<img src={getPath('./img/main/slides/slide_4.jpg')}
									alt="slide-4" />)
							}
						</picture>
					</div>
					<div className="main-slide__content _container">
						<div className="main-slide__title el-slidetitle">
							<span>  </span> 
						</div>
						<div className="main-slide__text t_01901">
							   , &nbsp;  
							&nbsp; .    
							 &nbsp;  &nbsp;,
							&nbsp;&nbsp;   .  
							&nbsp; &nbsp;   &nbsp;
							.
						</div>
					</div>
				</div>
				<div className="main-slide__slide-wrapper">
					<div className="main-slide__slide-image _img">
						<picture>
							{isWebpSupported()
								? (<img src={getPath('./img/main/slides/slide_2.webp')}
									alt="slide-2" />)
								: (<img src={getPath('./img/main/slides/slide_2.jpg')}
									alt="slide-2" />)
							}
						</picture>
					</div>
					<div className="main-slide__content _container">
						<div className="main-slide__title el-slidetitle">
							<span>  </span>  
						</div>
						<div className="main-slide__text t_01901">
							        , 
							     .  
							    ,   .
						</div>
					</div>
				</div>
				<div className="main-slide__slide-wrapper">
					<div className="main-slide__slide-image _img">
						<picture>
							{isWebpSupported()
								? (<img src={getPath('./img/main/slides/slide_3.webp')}
									alt="slide-3" />)
								: (<img src={getPath('./img/main/slides/slide_3.jpg')}
									alt="slide-3" />)
							}
						</picture>
					</div>
					<div className="main-slide__content _container">
						<div className="main-slide__title el-slidetitle">
							<span> </span>  
						</div>
						<div className="main-slide__text t_01901">
							     ,     
							,   !     
							 ,        
							.
						</div>
					</div>
				</div>
			</div>
			<div className="main-slide__pagination"></div>
			<div className="main-slide__media">
				<AudioPlayer />
			</div>
		</div>
	);
};

MainSlide.propTypes = {
	baseUrl: PropTypes.string.isRequired,
};
